import React from "react";
import Card from "react-bootstrap/Card";
import { ImPointRight } from "react-icons/im";

function AboutCard() {
  return (
    <Card className="quote-card-view">
      <Card.Body>
        <blockquote className="blockquote mb-0">
          <p style={{ textAlign: "justify" }}>
            I am from <span className="purple">Taguig, Metro Manila </span>
            currently engaged as a <span className="purple"> Data Analyst </span> for a web retail company headquartered in Vancouver, Canada.
            <br />
            <br />I have almost a decade of collective work experience in the fields of Accounting, Compliance, and Data Analytics.
            <br />
            <br />
            <br />
            <br />
            <br />
            <br />
            During my personal down time, you usually find me playing...
            <br />
            <br />
          </p>
          <ul>
            <li className="about-activity">
            <h1 style={{ paddingBottom: 15, textAlign: "left" }} className="heading">
                Valorant <span className="wave" role="img" aria-labelledby="wave">🎮</span>
              </h1>
            </li>
            <li className="about-activity">
            <h1 style={{ paddingBottom: 15, textAlign: "left" }} className="heading">
                With dogs <span className="wave" role="img" aria-labelledby="wave">🐶</span>
              </h1>
            </li>
            <li className="about-activity">
            <h1 style={{ paddingBottom: 15, textAlign: "left" }} className="heading">
                The guitar <span className="wave" role="img" aria-labelledby="wave">🎸</span>
              </h1>
            </li>
          </ul>

          
        </blockquote>
      </Card.Body>
    </Card>
  );
}

export default AboutCard;
